@font-face {
    font-family: "DungGeunMo";
    src: url(./assets/fonts/DungGeunMo.ttf);
}

* {
    font-family: "DungGeunMo";
    // background-color: rgb(53, 53, 53);
    // background-color: white;
    font-size: 25px;
    margin: 0;
    font-weight: 500;
    // color: #ddd;
    color: black;
    // background: transparent;
    background-color: #d0f561;

}

.header {
    width: 100%;
    height: 120px;
    // min-width: 734px;
    background-color: black;
    // position: fixed;
    top: 0;
    z-index: 999;

    .HD {
        // width: 90%;
        // height: 100%;
        // display: flex;
        // justify-content: space-between;
        // align-items: center;
        // margin: 0 auto;
        width: 100%;
        height: 120px;
        background-color: transparent;

        .HDtext {
            color: white;
            font-size: 1rem;
            position: absolute;
            right: 30px;
            top: 20px;
            background-color: black;
        }

        .LOGO {
            // width: 126px;
            color: white;
            font-size: 3rem;
            position: absolute;
            left: 30px;
            top: 20px;
            background-color: black;
        }

        .SEARCH {
            position: absolute;
            top: 150px;
            left: 0;
            width: 100%;

            input {
                width: 350px;
                min-width: 100px;
                height: 30px;
                border: 3px solid;
                border-radius: 20px;

            }

            form {
                text-align: center;
                display: flex;
                justify-content: center;
            }
        }
    }

    button {
        // border: 1px solid #ddd;
        border: 3px solid;
        width: 40px;
        height: 40px;
        padding: 0;
        font-size: 0.8rem;
        border-radius: 100%;
        margin-left: 25px;
    }

}


.VideoList {
    position: relative;
    top: 115px;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1%;

    .bg {
        position: absolute;
    }

    // .bg {
    //     width: 100%;
    //     height: 100%;
    //     position: absolute;
    //     top: 0;
    //     left: 0;
    //     z-index: -1;
    //     background: url('../public/img/Outrun\ -\ the\ retrofuturistic\ dreams\ of\ 1985_\ â€¢\ r_outrun.gif') no-repeat center / contain;
    //     // background-repeat: no-repeat;
    //     // background-size: auto;  
    //     background-attachment: fixed;   
    //     background-size: 100%;
    // }

    .HotRank {
        z-index: 1;
        background-color: transparent;
        position: absolute;
        // width: 100%;
        // left: 0px;
        top: -100px;
        left: 50px;
        text-align: center;
        margin-bottom: 15px;
        padding-bottom: 15px;
        padding-top: 15px;
        font-size: 2rem;
        display: flex;
        justify-content: center;

        .Fire {

            position: relative;
            top: 4px;
            width: 40px;
            height: 40px;
            background-color: transparent;
            // background: url('../public/img/firePNG.png') left center;
            background: url('../public/img/KakaoTalk_20230210_162331727.gif') left center;
            background-size: 150px;
            animation: play .6s steps(4) infinite;

            @keyframes play {
                100% {
                    background-position: -450px;
                }
            }
        }
    }

    a {
        width: 23%;
        // height: 480px;
        margin-bottom: 65px;
        max-width: 352px;
        min-width: 264px;
    }

    li {
        width: 100%;
        display: flex;
        flex-direction: column;
        color: #ddd;

        img {
            border-radius: 10%;
        }

        h2 {
            margin-bottom: 20px;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            font-size: 20px;
            margin-bottom: 10px;
            margin-top: 5px;
        }

        h4 {
            margin-top: 0px;
            font-size: 18px;
        }
    }

    // .bg {
    // }

}

@media screen and (max-width : 767px) {
    .VideoList {
        a {
            width: 45%;
        }
    }
}

@media screen and (max-width : 495px) {
    .VideoList {
        a {
            width: 100%;
            min-width: 390px;
            min-height: 292px;
        }
    }

}

