.bottom {
    width: 100%;
    height: 60px;
    background-color: black;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    position: fixed;
    bottom: 0;
    z-index: 999;
    color: #ddd;


    .BT {
        width: 100%;
        max-width: 720px;
        display: flex;
        justify-content: center;
        background-color: black;

        .HOME {
            background-color: black;
            color: white;
        }
    }
}

.VideoPlay {
    width: 100%;
    max-width: 720px;
    // min-width: 720px;
    position: relative;
    top: 150px;
    margin: 0 auto;

    .frame {
        width: 100%;
        max-width: 720px;
        height: 50px;
        background-color: white;
        border: 2px solid rgb(10, 10, 10);
        border-bottom: 2px solid;
        border-top-left-radius: 25px;
        border-top-right-radius: 25px;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        box-shadow: 15px 15px #CCCCCC;

        .TitleText {
            width: 85%;
            background-color: transparent;



            p {
                font-size: 17px;
                background-color: transparent;
            }
        }

        .xx {
            display: flex;
            width: 7%;
            background-color: transparent;

            img {
                background-color: transparent;
                width: 24px;
            }
        }


    }

    .player {
        width: 100%;
        max-width: 720px;
        // border: 1px solid #ddd;
        border: 2px solid rgb(10, 10, 10);
        background-color: white;
        height: 500px;
        box-shadow: 15px 15px #CCCCCC;
        padding: 40px 0;
    }


    .textbox {
        width: 100%;
        // border: 1px solid #ddd;
        border-bottom: 1px solid rgb(10, 10, 10);
        margin-top: 50px;



        h2 {
            margin-bottom: 40px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            color: black;
        }

        p {
            font-size: 20px;
            line-height: 25px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            // color: #ddd;
            color: black;
        }





        span {
            font-weight: 300;
        }

    }

    .VCback {
        width: 728px;
        height: 500px;
        position: relative;
        top: 10px;
        margin-bottom: 445px;


        .HotRank {
            z-index: 22;
            background-color: transparent;
            position: absolute;
            top: 85px;
            left: 80px;
            text-align: center;
            margin-bottom: 15px;
            padding-bottom: 15px;
            padding-top: 15px;
            font-size: 2rem;
            display: flex;
            justify-content: center;

            .Fire {

                position: relative;
                top: 4px;
                width: 40px;
                height: 40px;
                background-color: transparent;
                // background: url('../public/img/firePNG.png') left center;
                background: url('../public/img/KakaoTalk_20230210_162331727.gif') left center;
                background-size: 150px;
                animation: play .6s steps(4) infinite;

                @keyframes play {
                    100% {
                        background-position: -450px;
                    }
                }
            }
        }

        .a {
            width: 100%;
            height: 335px;
            position: absolute;
            bottom: 0;
            background-color: white;
            z-index: 9;
            border: 3px solid;

            .tap1 {
                width: 247px;
                height: 75px;
                position: absolute;
                left: -3px;
                top: -77px;
                background-color: white;
                border: 3px solid;
                border-bottom: 4px solid;
                border-top-left-radius: 50px;
                border-top-right-radius: 50px;
            }

            .tap2 {
                width: 242px;
                height: 75px;
                position: absolute;
                left: 242px;
                top: -77px;
                background-color: gray;
                border: 3px solid;
                border-bottom: 4px solid;
                border-top-left-radius: 50px;
                border-top-right-radius: 50px;
                font-size: 2rem;
                display: flex;
                justify-content: center;
                align-items: center;
            }

            .tap3 {
                width: 242px;
                height: 75px;
                position: absolute;
                left: 483px;
                top: -77px;
                background-color: gray;
                border: 3px solid;
                border-bottom: 4px solid;
                border-top-left-radius: 50px;
                border-top-right-radius: 50px;
                font-size: 2rem;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }

        h2 {
            width: 100%;
            position: absolute;
            top: 230px;
            z-index: 22;
            background-color: transparent;
            display: flex;
            justify-content: center;
        }


        .VideoCategories {
            z-index: 10;
            width: 100%;
            display: flex;
            overflow-x: scroll;
            position: relative;
            top: 285px;
            background-color: transparent;

            img {
                margin-left: 30px;
                width: 200px;
                height: 140px;
            }
        }
    }

}


.Search {
    position: relative;
    top: 195px;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1%;

    .text {
        position: absolute;
        top: -70px;
        font-size: 1.2rem;
    }

    a {
        width: 20%;
        min-width: 264px;
    }

    .cont {
        text-align: center;
        width: 100%;
        // min-width: 389px;
        margin-bottom: 50px;

        li {
            // color: #ddd;
            color: black;
            // font-weight: 500;
            text-align: left;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            font-size: 20px;
            margin-bottom: 4px;

        }

        img {
            border-radius: 10%;
            width: 100%;
            // height: 244px;
            // max-height: 245px;
        }
    }
}


@media screen and (max-width : 676px) {
    .header {
        .HD {
            .HDtext {
                display: none;
            }
        }
    }

    .VideoPlay {
        width: 96%;

        .player {
            box-shadow: none;
        }

        .frame {
            box-shadow: none;

            .TitleText {
                width: 73%;
            }
        }

        .VCback {
            display: none;
        }

        .textbox {
            margin-bottom: 300px;
        }
    }

    .Search {
        a {
            width: 85%;
        }
    }
}